.form {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.form input,
.form button {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.form button {
  background: #4f46e5;
  color: white;
  border: none;
  cursor: pointer;
}

.form button:hover {
  background: #3730a3;
}

@media (max-width: 640px) {
  .form {
    flex-direction: column;
  }
  .form input,
  .form button {
    width: 100%;
  }
}
